        @Watch('streamEnd') _toggleVideoModal(){
            if(this.streamEnd == true) this.modalEnd.open();
            else{this.modalEnd.close();}
		}

		public mounted() {

			this.socketConnect();
	
			this.player = window.videojs('web-video-id', {
				controls: false,
				responsive:true, controlBar: {
					pictureInPictureToggle: false,
					durationDisplay: false, timeDivider: false, liveDisplay: false,
					fullscreenToggle: false, 
					progressControl: false
				}
			});

			this.remoteVideo = this.player.tech().el();
			this.player.ready(() => {
				this.player.controlBar.playToggle.on('click', (e) => {
					this.isPlaying? this._pause() : this._play();
				});
			});

			this.player.on('timeupdate', (e) => 
				this.currentTime = this.formatTime(this.player.currentTime())
			);

            var ModalDialog = window.videojs.getComponent('ModalDialog');
            this.modalEnd = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalEnd.addClass('not-started-modal');
            this.player.addChild(this.modalEnd);
			
			this.modalPause = new ModalDialog(this.player, {temporary: false, uncloseable: true});
            this.modalPause.addClass('pause-modal');
            this.player.addChild(this.modalPause);
			
			this.modalEnd.open();

			window.onbeforeunload = () => {
				if(this.intervalId != null) clearInterval(this.intervalId);
			}
			
			window.addEventListener('sendquestion', (e:any) => {
				this.question = {...this.question, name: e.detail['name'], content: e.detail['content'] }
				this.sendQuestion();
			});
		}

		sendQuestion() {
			if(this.streamEnd) return;
			let message = {
				textroom: 'message', to: 'pastor',
				transaction: this.randomString(12), room: 7305, text: JSON.stringify(this.question) //.content,
			}
			this.textHandle.data({text: JSON.stringify(message)})
            this.question = {};
			this.askQuestionModal = false;
		}

        sendChat() {
			if(this.chat.content == '') return;
			let message = {
				textroom: 'message', transaction: this.randomString(12), room: 7305, text: JSON.stringify(this.chat) //.content
			}
			this.textHandle.data({text: JSON.stringify(message)})
            this.chat={...this.chat, content:''};
		}

		_play() {
			this.remoteVideo.play();
			this.isPlaying = true;
		}

		_pause() {
			this.remoteVideo.pause(); 
            this.isPlaying = false;
		}

		_mute() {
			this.remoteVideo.muted = true;
			this.isMuted = true;
		}

		_unmute() {
			this.remoteVideo.muted = false; 
			this.isMuted = false;
		}

		registerChat() {
			this.janus.attach({
				plugin: 'janus.plugin.textroom',
				success: (handle) =>{
					this.textHandle = handle;
					this.textHandle.send({message: {request: 'setup'}});
				},

				onmessage: (message, desc) =>{
					if(desc) this.textHandle.createAnswer({
						jsep: desc, media: {audio: false, video: false, data: true },
						success: (jsep) => this.textHandle.send({ message:{ request: 'ack'}, jsep }),
						error: (error) => {}
					})
				},
				ondata: (data) => {
					//bn,data = JSON.parse(data);
					//if(data['textroom'] == 'message') this.chatList=[...this.chatList, JSON.parse(data.text)]
				},
				ondataopen: () => {
					let register = {textroom: 'join', room: 7305, username: this.randomString(12), display: this.randomString(12), transaction: this.randomString(12)};
					this.textHandle.data({text: JSON.stringify(register)});
				}
			});
			this.chat = { ...this.chat, name};
		}

		registerStream() {
			this.janus.attach({
				plugin: 'janus.plugin.videoroom',
				success: (handle) =>{
					this.sfuHandle = handle;
					var subscriber = { request: 'join', room: 1, ptype: 'subscriber', feed: 27052 };
					this.sfuHandle.send({message: subscriber});
				},

				onmessage: (message, desc) =>{
					if(desc) this.sfuHandle.createAnswer({
						jsep: desc, media: {audioSend: false, videoSend: false, data: true },
						success: (jsep) => this.sfuHandle.send({ message:{ request: 'start', room: 1}, jsep }),
						error: (error) => {}
					})
				},
				ondata: (d) => {
					if(this.streamEnd) return;
					let data = JSON.parse(d);
					if(data['verse'])
						if(data['show'] == true) {
							this.verse = data; this.showVerse = true;
						} else { this.showVerse = false; }
					
					if(data['banner'])
						if(data['show'] == true) {
							this.banner = data; this.showBanner = true;
						} else { this.showBanner = false; }

				},
				onremotestream: (stream) => window.Janus.attachMediaStream(this.remoteVideo, stream)
			});

			/*this.intervalId = setInterval(() => this.sfuHandle.send({
				message:{ janus: 'keepalive', session_id: this.janus.getSessionId(), transaction: this.randomString(12)}
			}), 50000);*/
		}

		randomString(len) {
			let charSet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
			var randomString = '';
			for (var i = 0; i < len; i++) {
				var randomPoz = Math.floor(Math.random() * charSet.length);
				randomString += charSet.substring(randomPoz,randomPoz+1);
			}
			return randomString;
		}
	
		streamStarted(started: string) {
			switch(started) {
				case "yes":{
					this.streamEnd = false; 
					if(this.modalEnd != null) this.modalEnd.close();
					window.Janus.init({
						debug: false, dependencies: window.Janus.useDefaultDependencies(), callback: () => {
							this.janus = new window.Janus({
								server: LiveStream._socketServer,
								iceServers: this.servers.iceServers,
								success: () => { this.registerStream(); this.registerChat(); },
								error: (cause) => console.log('CAUSE cannt create JANU ' + cause)
							})	
						}
					});		
				}
				break;
				case "no":{
					/*this.streamEnd = true;
					this.isPlaying = false;
					if(this.sfuHandle != null)  this.sfuHandle.send({message:{request: 'leave'}});
					if(this.textHandle != null) this.textHandle.send({message:{request: 'leave'}});

					let leave = { textroom: 'leave', room: 7305, transaction: this.randomString(12) };
					this.textHandle.data({text: JSON.stringify(leave)});
					this.textHandle = null;*/


					if(!this.streamEnd)  {
						this.streamEnd = true;
						this.isPlaying = false;
						this.sfuHandle.send({message:{request: 'leave'}});
						this.sfuHandle = null;

						let leave = { textroom: 'leave', room: 7305, transaction: this.randomString(12) };
						this.textHandle.data({text: JSON.stringify(leave)});
						this.textHandle = null;
					};
				}
				break;
				case "pause":{
                    this.player.muted(true);
					this.modalPause.open();
				}
				break;
				case "resume":{
					this.modalPause.close();
                    this.player.muted(false);
                    this._play();
				}
				break;
			}
		}
	
		socketConnect() {
			let ssocket = new WebSocket(process.env.VUE_APP_WSS);

			ssocket.onopen = () => {
				if(this.global_timer_id != null) clearTimeout(this.global_timer_id); 
				ssocket.send('video-started')
			}

			ssocket.onmessage = (m) => {
				m = JSON.parse(m.data);
				if(m['videostarted']) this.streamStarted(m['videostarted']);
			}

			ssocket.onclose = (c) => 
				this.global_timer_id = setTimeout(() => {
					this._pause();
					ssocket = null; this.socketConnect()
				}, 3000);
			ssocket.onerror = (e) => ssocket.close();
		}

		beforeDestroy() {
			if(this.player) this.player.dispose();
		}

		formatTime(seconds, guide = seconds) {
			 seconds = seconds < 0 ? 0 : seconds;
			let s = Math.floor(seconds % 60);
			let m = Math.floor(seconds / 60 % 60);
			let h = Math.floor(seconds / 3600);
			const gm = Math.floor(guide / 60 % 60);
			const gh = Math.floor(guide / 3600);
			
			// handle invalid times
			if (isNaN(seconds) || seconds === Infinity) {
				h = m = s = 0 ; 
			}

			var hours = (h > 0 || gh > 0) ? h + ':' : '';
			var minute  = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':';
			var second = (s < 10) ? '0' + s : s;

			return hours + minute + second;
		}

}
